.CallMe-white-popup {
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 0px 50px 0px rgba(29, 34, 38, 0.05), 0px 0px 15px 0px rgba(29, 34, 38, 0.05);
    width: 230px;
    height: 200px;
    border-radius: 25px;
    background-size: contain;
    position: absolute;
    left: 6px;
    top: 56px;
    z-index: 122;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.CallMe-white-popup:before {
    content: '';
    background: url(/static/media/popup_nipple.ba401ca1.svg);
    width: 64px;
    height: 62px;
    position: absolute;
    top: -58px;
    left: 83px;
}

.CallMe-white-popup-input {
    position: relative;
    margin-bottom: 30px;
    width: 190px;
}

.CallMe-white-popup-input input {
    font-size: 16px;
    padding-bottom: 10px;
    display: block;
    width: 100%;
    border: none;
    color: #333333;
    border-bottom: 2px solid #CBD9F7;
    background-color: transparent;
}

.CallMe-white-popup-input input:focus {
    outline: none;
}

.CallMe-white-popup-input label {
    color: #babfc2;
    font-size: 19px;
    position: absolute;
    pointer-events: none;
    left: 0;
    width: 100%;
    top: -3px;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
    font-style: italic;
}

.CallMe-white-popup-input input:focus ~ label,
.CallMe-white-popup-input input:valid ~ label {
    top: -18px;
    font-size: 14px;
    color: #295FDE;
}

.CallMe-white-popup-input span {
    position: relative;
    display: block;
    width: 100%;
}
.CallMe-white-popup-input span:before,
.CallMe-white-popup-input span:after {
    content: "";
    height: 2px;
    width: 0;
    bottom: 0;
    position: absolute;
    background: #295FDE;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}
.CallMe-white-popup-input span:before {
    left: 50%;
}
.CallMe-white-popup-input span:after {
    right: 50%;
}

/* showForm state */
.CallMe-white-popup-input input:focus ~ span:before,
.CallMe-white-popup-input input:focus ~ span:after {
    width: 50%;
}

.CallMe-white-popup-input-group {
    padding: 20px 20px 0 20px;
